// // app/history/page.tsx
"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  ChevronDownIcon,
  ChevronRightIcon,
  EyeIcon,
  TrashIcon,
  DocumentArrowDownIcon,
  MagnifyingGlassIcon,
  ClockIcon,
} from "@heroicons/react/24/outline";

type ComparisonItem = {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: "HIGH" | "MEDIUM" | "LOW" | null;
  html_report_url?: string | null;
  json_report_url?: string | null;
};

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() || "http://127.0.0.1:8000";

export default function HistoryPage() {
  const [items, setItems] = useState<ComparisonItem[]>([]);
  const [openDocs, setOpenDocs] = useState<Record<string, boolean>>({});
  const [search, setSearch] = useState("");

  useEffect(() => {
    fetch(`${API_BASE}/comparisons?limit=100`)
      .then((r) => r.json())
      .then(setItems);
  }, []);

  const grouped = useMemo(() => {
    const map: Record<string, ComparisonItem[]> = {};
    items.forEach((i) => {
      if (!i.document_name.toLowerCase().includes(search.toLowerCase())) return;
      map[i.document_name] ??= [];
      map[i.document_name].push(i);
    });
    return map;
  }, [items, search]);

  const toggle = (name: string) =>
    setOpenDocs((p) => ({ ...p, [name]: !p[name] }));

  const Risk = ({ r }: { r?: string | null }) => {
    if (r === "HIGH") return <span className="text-red-600">HIGH</span>;
    if (r === "MEDIUM") return <span className="text-amber-600">MED</span>;
    if (r === "LOW") return <span className="text-emerald-600">LOW</span>;
    return <span className="text-slate-400">-</span>;
  };

  return (
    <div className="max-w-5xl mx-auto p-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h1 className="text-xl font-bold flex items-center gap-2">
          <ClockIcon className="h-5 w-5" />
          ประวัติการเปรียบเทียบ
        </h1>
        <Link href="/" className="text-blue-600 font-semibold">
          เปรียบเทียบใหม่
        </Link>
      </div>

      {/* Search */}
      <div className="flex items-center gap-2 border rounded-lg px-3 py-2 bg-white">
        <MagnifyingGlassIcon className="h-4 w-4 text-slate-400" />
        <input
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          placeholder="ค้นหาชื่อเอกสาร..."
          className="flex-1 outline-none text-sm"
        />
      </div>

      {/* Grouped list */}
      <div className="space-y-3">
        {Object.entries(grouped).map(([doc, list]) => (
          <div key={doc} className="border rounded-xl bg-white">
            <button
              onClick={() => toggle(doc)}
              className="w-full flex items-center gap-2 px-4 py-3 hover:bg-slate-50"
            >
              {openDocs[doc] ? (
                <ChevronDownIcon className="h-4 w-4" />
              ) : (
                <ChevronRightIcon className="h-4 w-4" />
              )}
              <span className="font-semibold">{doc}</span>
              <span className="text-xs text-slate-500">
                ({list.length})
              </span>
            </button>

            {openDocs[doc] && (
              <div className="divide-y">
                {list.map((i) => (
                  <div
                    key={i.id}
                    className="px-6 py-3 flex items-center justify-between text-sm"
                  >
                    <div className="flex items-center gap-3">
                      <Risk r={i.overall_risk_level} />
                      <span>
                        {i.version_old_label} → {i.version_new_label}
                      </span>
                    </div>

                    <div className="flex items-center gap-1">
                      <Link
                        href={`/compare/${i.id}`}
                        className="p-1 hover:bg-slate-100 rounded"
                      >
                        <EyeIcon className="h-4 w-4" />
                      </Link>
                      {i.html_report_url && (
                        <a
                          href={`${API_BASE}${i.html_report_url}`}
                          target="_blank"
                          className="p-1 hover:bg-slate-100 rounded"
                        >
                          <DocumentArrowDownIcon className="h-4 w-4" />
                        </a>
                      )}
                      {i.json_report_url && (
                        <a
                          href={`${API_BASE}${i.json_report_url}`}
                          target="_blank"
                          className="p-1 hover:bg-slate-100 rounded"
                        >
                          <DocumentArrowDownIcon className="h-4 w-4" />
                        </a>
                      )}
                      <button className="p-1 hover:bg-red-50 rounded">
                        <TrashIcon className="h-4 w-4 text-red-600" />
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}
